.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.textContainer {
  position: absolute;
  width: 100%;
  text-align: center;
  background-color: white;
  opacity: 0.8;
  padding: 16px;
}


@font-face {
  font-family: 'CustomFont';
  src: url('../src/assets/font/melloner-fun-font/MellonerFun-d1wE.ttf');
}

@media (max-width: 959px) {
  .call-us {
    width: 80%;
    left: 10%;
    bottom: 20%;
  }
}

@media (max-width: 959px) {
  #stats-counter {
 display: none !important;
  }
}

@media (max-width: 830px) {
  .section-bg {
 display: none !important;
  }
}



  

  
